.job-icon {
  float: left;
  margin-right: 4px;
  height: 20px;
}
.job-icon img {
  margin-top: 1px;
  height: 20px;
  width: 20px;
}
.character-job {
  margin-left: 4px;
  text-transform: uppercase;
  opacity: 0.6;
}
.character-name {
  padding-left: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.inactive .bar {
  background-color: #d8dadb;
}

.arrow {
  cursor: pointer;
  display: inline-block;
  height: 14px;
  width: 14px;
}

.arrow:after {
  clear: both;
  display: block;
  position: relative;
  top: 12px;
  left: 7px;
  content: "";
  height: 0;
  width: 0;
  padding: 0;
  margin: -4px;
  border:4px solid transparent;
}
.arrow.down:after {
  border-top-color: #fff;
}
.arrow.up:after {
  top: 8px;
  border-bottom-color: #fff;
}

.hidden {
  display: none;
}

.dropdown-parent {
  position: relative;
}

.dropdown-menu {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 20px;
  left: 0px;
  padding: 2px 4px;
  z-index: 100;
}

.clearfix:before,
.clearfix:after{
display:table;
content:'';
}
.clearfix:after{
clear:both;
}
